@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:ital,wght@1,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  background: #fafafa !important;
  padding: 0.4em;
}
.App {
  background: transparent;
  text-align: center;
}
.center {
  display: flex;
  justify-content: center;
  align-items: center;
}
#receiptsContainer {
  margin: 0.8em;
}
.spinner-border.text-primary {
  margin: 1em;
}
.card-subtitle {
  font-size: 1em !important;
}
.card {
  font-size: 0.8em;
  width: 18rem;
  padding: 1em 0em 0em 0em;
  -webkit-animation-name: animateCards;
          animation-name: animateCards;
  -webkit-animation-duration: 400ms;
          animation-duration: 400ms;
  -webkit-animation-delay: calc(var(--animation-order) * 100ms);
          animation-delay: calc(var(--animation-order) * 100ms);
  -webkit-animation-fill-mode: both;
          animation-fill-mode: both;
  -webkit-animation-timing-function: ease-in-out;
          animation-timing-function: ease-in-out;
}
.card-body {
  padding: 0.2em !important;
}
@-webkit-keyframes animateCards {
  0% {
    opacity: 0;
    transform: scale(0.5) translateY(-10px);
  } 
  100% {
    opacity: 1;
  }
}
@keyframes animateCards {
  0% {
    opacity: 0;
    transform: scale(0.5) translateY(-10px);
  } 
  100% {
    opacity: 1;
  }
}
.alert-success {
  border-radius: 4px;
  padding: 0.2em;
}
.list-group-item {
  font-size: 0.9em;
  padding: .25rem 0.5rem !important;
}
.list-group-horizontal:nth-child(1) {
  padding-top: 0.8em;
}
p {
  margin: 0 !important;
  width: 45vw;
}
@media only screen and (max-width : 720px) {
  p { width: 90vw; }
}
